.projects {
    min-height: 100vh;
    background-color: #9242d1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    width: 90%;
    margin: 30px auto;
    border: 2px solid;
    display: flex;
    align-items: center;
    flex-direction: column;

}

.heading {
    width: 100%;
    height: 100px;
    border: 1px solid;
    text-align: center;
}

.myProjects {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

